<template>
  <section class="support-section">
    <div class="bg"></div>
    <div class="box">
      <div class="container">
        <div class="marketing">
          <span class="text">
            <span class="marketing-text"
              ><span>MARKETING</span>
              <v-icon v-if="text == 0" class="icon">mdi-arrow-right</v-icon>
            </span>
          </span>
        </div>
        <div class="manage">
          <span class="text manage-text"
            ><span>MANAGE</span
            ><v-icon v-if="text == 1" class="icon"
              >mdi-arrow-right</v-icon
            ></span
          >
        </div>
        <div class="service">
          <span class="text service-text"
            ><span>SERVICE</span
            ><v-icon v-if="text == 2" class="icon"
              >mdi-arrow-right</v-icon
            ></span
          >
        </div>
      </div>
    </div>
    <div class="hover-circle">
      <!-- <span v-if="text === 0" class="marketing hover-text">MARKETING</span>
      <span v-if="text === 1" class="manage hover-text">MANAGE</span>
      <span v-if="text === 2" class="service hover-text">SERVICE</span> -->
    </div>
  </section>
</template>

<script>
import { gsap, ScrollTrigger } from "gsap/all";
export default {
  name: "SupportPage",
  data() {
    return {
      text: -1,
    };
  },

  mounted() {
    const marketingText = document.querySelector(".marketing-text");
    const manageText = document.querySelector(".manage-text");
    const serviceText = document.querySelector(".service-text");
    const hoverCircle = document.querySelector(".hover-circle");

    marketingText.addEventListener("mouseover", () => {
      this.text = 0;
    });
    marketingText.addEventListener("mouseleave", () => {
      this.text = -1;
    });
    manageText.addEventListener("mouseover", () => {
      this.text = 1;
    });
    manageText.addEventListener("mouseleave", () => {
      this.text = -1;
    });
    serviceText.addEventListener("mouseover", () => {
      this.text = 2;
    });
    serviceText.addEventListener("mouseleave", () => {
      this.text = -1;
    });

    let text = document.querySelectorAll(".text");

    // hover시에 원그리기
    // const sectionOffset = 4000; // 400vh
    let main = document.querySelector(".main-section");
    let stiky = document.querySelector(".sticky-section");
    const small_circle_rect = 12.5;
    const sectionOffset = main.clientHeight + stiky.clientHeight;

    text.forEach((el) => {
      el.addEventListener("mouseover", function () {
        let mouseX = 0;
        let mouseY = 0;

        function updateCirclePosition() {
          hoverCircle.style.left = mouseX + "px";
          hoverCircle.style.top = mouseY + "px";
        }

        function handleMouseMove(event) {
          const scrollY = window.pageYOffset;
          mouseX = event.clientX - small_circle_rect;
          mouseY = event.clientY + scrollY - sectionOffset - small_circle_rect;
          // 마우스 포인터 위치가 뷰포트 바깥으로 나가지 않도록 보정
          mouseX = Math.max(
            0,
            Math.min(
              mouseX,
              window.innerWidth - hoverCircle.getBoundingClientRect().width
            )
          );
          mouseY = Math.max(
            0,
            Math.min(
              mouseY,
              window.innerHeight - hoverCircle.getBoundingClientRect().height
            )
          );
        }

        function loop() {
          updateCirclePosition();
          requestAnimationFrame(loop);
        }

        hoverCircle.style.scale = 8;
        document.addEventListener("mousemove", handleMouseMove);
        loop();
      });
      el.addEventListener("mouseleave", function () {
        hoverCircle.style.scale = 1;
      });
    });

    //스크로 초기 애니메이션
    gsap.registerPlugin(ScrollTrigger);

    const support = gsap.timeline({ paused: true });
    support
      .to(".marketing", { duration: 1.5, opacity: 1 }, "start")
      .to(
        ".marketing",
        { duration: 1.5, transform: "translateX(10%)" },
        "start"
      )
      .to(".manage", { duration: 1.7, opacity: 1 }, "start+=.2")
      .to(
        ".manage",
        { duration: 1.7, transform: "translateX(10%)" },
        "start+=.2"
      )
      .to(".service", { duration: 2, opacity: 1 }, "start+=.4")
      .to(
        ".service",
        { duration: 2, transform: "translateX(10%)" },
        "start+=.4"
      );

    gsap.timeline({
      scrollTrigger: {
        trigger: ".support-section",
        start: "top 40%",
        scrub: true,
        animation: support,
        onEnter: () => {
          support.play();
        },
      },
    });
  },
};
</script>

<style lang="scss" scoped>
.support-section {
  cursor: none;
  width: 100%;
  height: 100vh;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.bg {
  width: 100%;
  height: 100vh;
  z-index: -1;
  position: absolute;
  background-color: rgb(255, 255, 255);
  // background-image: url("@/assets/Main/sky.jpg");
  background-size: cover;
}
.hover-circle {
  position: absolute;
  width: 25px;
  height: 25px;
  border-radius: 100%;
  background-color: white;
  transition-property: scale;
  transition-duration: 0.5s;
  pointer-events: none;
  display: flex;
  justify-content: center;
  align-items: center;
  mix-blend-mode: difference; /* 특정 엘리먼트의 색상을 반전시킴 */
  .hover-text {
    font-size: 0.89rem;
    font-family: "Pretendard";
    font-weight: bold;
    color: black;
  }
}

.box {
  width: 100%;
  height: 75%;
  display: flex;
  position: relative;
}
.container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;

  .marketing {
    display: flex;
    align-items: center;
    width: 100%;
    height: 25%;
    opacity: 0;
    transform: translateX(40%);
    .marketing-text {
      flex-wrap: nowrap;
      display: flex;
      align-items: center;
    }
  }
  .manage {
    display: flex;
    align-items: center;
    width: 100%;
    height: 25%;
    opacity: 0;
    transform: translateX(40%);
    .manage-text {
      flex-wrap: nowrap;
      display: flex;
      align-items: center;
    }
  }
  .service {
    display: flex;
    align-items: center;
    height: 25%;
    width: 100%;
    opacity: 0;
    transform: translateX(40%);
    .service-text {
      flex-wrap: nowrap;
      display: flex;
      align-items: center;
    }
  }
  .icon {
    font-size: 10rem;
  }
  .text {
    font-family: "Pretendard";
    font-weight: bold;
    font-size: 11rem;
    color: rgb(0, 0, 0);
  }
}
.text:hover {
  overflow: hidden;
}
</style>
