<template>
  <section class="sticky-section">
    <div class="bg"></div>
    <div class="content">
      <div class="content-left">
        <img src="" class="" />
      </div>
      <div class="content-right">
        <div class="sticky-con1">
          <div class="line"></div>
          <span class="first-text">창의적이고 개성있는 콘텐츠를 만들고 </span>
        </div>
        <div class="sticky-con2">
          <span class="second-text">
            <p padding>크리에이터와 협력하여</p>
            <p>브랜드의 가치를 높이는 기업</p>
          </span>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import { gsap, ScrollTrigger } from "gsap/all";
export default {
  name: "StikyPadge",
  data: () => ({
    image: require("@/assets/Header/logo.png"),
  }),
  mounted() {
    gsap.registerPlugin(ScrollTrigger);

    const stykyText1 = gsap.timeline({
      scrollTrigger: {
        trigger: ".sticky-section",
        pin: false,
        start: "top 0%",
        end: "+=110%",
        scrub: 1,
      },
    });
    stykyText1
      .to(
        ".sticky-con1",
        {
          delay: 0,
          y: 25,
          opacity: 1,
        },
        "start"
      )
      .to(
        ".sticky-con1",
        {
          delay: 0,
          y: 50,
          opacity: 0,
        },
        "+=.1"
      );

    const stykyText2 = gsap.timeline({
      scrollTrigger: {
        trigger: ".sticky-con2",
        pin: false,
        start: "top 0%",
        end: "+=110%",
        scrub: 1,
      },
    });
    stykyText2
      .to(
        ".sticky-con2",
        {
          delay: 0,
          y: 25,
          opacity: 1,
        },
        "start"
      )
      .to(
        ".sticky-con2",
        {
          delay: 0,
          y: 50,
          opacity: 0,
        },
        "+=.1"
      );
  },
};
</script>

<style lang="scss" scoped>
.sticky-section {
  position: relative;
  width: 100%;
  height: 320vh;
}
.bg {
  width: 100%;
  height: 320vh;
  z-index: -1;
  position: absolute;
  background-size: cover;
  background-image: url("@/assets/Main/sky.jpg");
}

.content {
  display: flex;
  width: 100%;
  height: 100%;

  .content-left {
    width: 50%;
  }

  .content-right {
    width: 50%;
    .sticky-con1 {
      height: 150vh;
      display: flex;
      justify-content: center;
      margin-top: 10%;
      opacity: 0;

      .first-text {
        position: sticky;
        display: grid;
        align-items: center;
        text-align: center;
        font-family: "Pretendard";
        font-weight: bold;
        font-size: 3rem;
        color: white;
        height: 130px;
        top: 30%;
      }
      // .line {
      //   width: 1px;
      //   height: 50vh;
      //   border-left: 1px solid white;
      // }
    }
    .sticky-con2 {
      height: 150vh;
      display: flex;
      justify-content: center;
      margin-top: 0%;
      opacity: 0;
      .second-text {
        position: sticky;
        display: grid;
        align-items: center;
        text-align: center;
        font-family: "Pretendard";
        font-weight: bold;
        font-size: 3rem;
        color: white;
        height: 130px;
        top: 30%;
      }
    }
  }
}

p {
  margin: 0;
}
</style>
