<template>
  <section class="main-section">
    <div class="bg"></div>
    <div class="main-title">
      <span class="main-text">BE A CREATER</span>
    </div>
  </section>
</template>

<script>
import { gsap, ScrollTrigger } from "gsap/all";
export default {
  name: "MainPage",
  mounted() {
    gsap.registerPlugin(ScrollTrigger);
    const MainTitleTimeline = gsap.timeline({
      scrollTrigger: {
        trigger: ".main-section",
        pin: false,
        start: "top top",
        end: "+=25%",
        scrub: 1,
      },
    });
    MainTitleTimeline.to(
      ".main-text",
      {
        delay: 0,
        opacity: 0,
        scale: 1.5,
        letterSpacing: "1.6rem",
      },
      "start"
    );
  },
};
</script>

<style lang="scss" scoped>
.main-section {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.bg {
  width: 100%;
  height: 100vh;
  z-index: -1;
  position: absolute;
  background-image: url("@/assets/Main/sky.jpg");
  background-size: cover;
}

.main-title {
  opacity: 1;
  text-align: center;
  animation: widen 1s forwards;
  .main-text {
    font-family: sans-serif;
    font-size: 13rem;
    font-weight: bold;
    color: white;
    letter-spacing: 0.1rem;
  }
}
@keyframes widen {
  from {
    letter-spacing: 0;
  }
  to {
    letter-spacing: 10px;
  }
}

